.team {
  padding: 70px 0 140px;
  background-color: #162344;
}

.team h2 {
  font-weight: 700;
  font-size: 50px;
  line-height: 60px;
  margin-bottom: 60px;
  text-align: center;
}

.emploee__foto {
  border-radius: 35px;
  overflow: hidden;
  margin-bottom: 20px;
}

.emploee__foto img {
  width: 100%;
}

.emploee__name {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 12px;
  text-align: center;
}

.emploee__position {
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  opacity: 0.6;
  text-align: center;
}

.swiper {
  padding-bottom: 67px;
}

.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
  background: #A2A7B4;
  width: 10px;
  height: 10px;
  border-radius: 0%;
  margin: 0 8px;
}

.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet-active {
  background: #FF0000;
}

.team__h2 {
  margin: 80px 0 40px;
}

.partners__logo {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.partners__logo {
  max-height: 80px;
  opacity: 0.6;
}

@media(max-width: 1325px) {
  .partners__logo {
     flex-wrap: wrap;
  }


}

@media(max-width: 720px) {
  .partners__logo {
      row-gap: 15px;
      column-gap: 5px;
   }

   .partners__logo {
      max-height: 32px;
      opacity: 0.6;
  }

  .partners__logo img:nth-child(1) {
      max-width: 100px;
      width: 100%;
  }

  .partners__logo img:nth-child(2) {
      max-width: 88px;
      width: 100%;
  }

  .partners__logo img:nth-child(3) {
      max-width: 105px;
      width: 100%;
  }

  .partners__logo img:nth-child(4) {
      max-width: 94px;
      width: 100%;
  }

  .partners__logo img:nth-child(5) {
      max-width: 121px;
      width: 100%;
  }

  .partners__logo img:nth-child(6) {
      max-width: 85px;
      width: 100%;
  }
}

@media(max-width: 514px) {
  .team {
      padding: 25px 0 90px;
  }

  .team h2 {
      margin-bottom: 15px;
  }

  .team__h2 {
      margin: 15px;
  }
}
