.spider-web {
  padding: 50px 0 320px;

}

.spider-web__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.spider-web__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 186px;
  flex-wrap: wrap;
}

.spider-web__logo img {
  filter: grayscale(100%);
  opacity: .1;
  max-height: 50px;
}

.spider-web p {
  font-weight: 700;
  font-size: 35px;
  line-height: 42px;
  max-width: 552px;
  text-align: center;
  z-index: 1;
  margin-bottom: 208px;
}

.spider-web__bg {
  position: absolute;
  top: 10px;
  max-width: 924px;
  width: 100%;
}

.spider-web__btn {
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  opacity: .9;
  background-color: var(--red);
  padding: 15px 45px;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  transition: .5s;
  display: block;
}

.spider-web__btn:hover {
  color: var(--red);
  background-color: var(--white);
}

@media(max-width: 1325px) {

  .spider-web p {
      font-size: 24px;
      line-height: 30px;
      max-width: 400px;
      order: 2;
      margin-bottom: 30px;
  }

  .spider-web__logo {
      order: 1;
  }

  .spider-web__bg {
      order: 3;
      position: relative;
  }

  .spider-web {
      padding: 50px 0 90px;
  }

  .spider-web__logo img {
      max-height: 30px;
  }

  .spider-web__logo {
      margin-bottom: 30px;
  }

  .spider-web__btn {
      position: absolute;
      bottom: -100px;
  }
}

@media(max-width: 720px) {
  .spider-web__btn {
      width: 100%;
      font-size: 20px;
  }
}
