.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: 0.5s;
}

header.active {
  box-shadow: 0 4px 20px rgba(0, 0, 0, .1);
  background-color: #101932;
}

.header__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__block img {
  max-width: 253px;
  width: 100%;
  margin-right: 180px;
}

nav {
  display: flex;
  justify-content: space-between;
  width: 60%;
}

nav .btn__community {
  display: none;
}

nav a {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  opacity: 0.8;
  border-bottom: solid 1px transparent;
  transition: 0.5s;
}

nav a:hover {
  border-color: var(--dark-red);
}

.btn__community {
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  opacity: 0.8;
  padding: 10px 30px;
  background-color: var(--dark-red);
  border-radius: 10px;
  margin-left: 70px;
  cursor: pointer;
  transition: 0.5s;
  display: block;
  white-space: nowrap;
}

.btn__community:hover {
  background-color: var(--white);
  color: var(--dark-red);
}

.burger__button {
  width: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: none;
}

.burger__button svg {
  width: 100%;
}

.menu__close {
  display: none;
}

@media(max-width: 1720px) {
  .header__block img {
    margin-right: 30px;
  }

  .btn__community {
    margin-left: 30px;
  }

  nav {
    width: 66%;
  }
}

@media(max-width: 1410px) {
  nav {
    display: none;
  }

  .btn__community {
    display: none;
    margin-bottom: 30px;
  }

  .header__block {
    justify-content: space-between;
  }

  .burger__button {
    display: block;
  }

  .menu__close {
    position: absolute;
    top: 40px;
    right: 22px;
    cursor: pointer;
  }

  nav.active {
    display: flex;
    overflow-y: scroll;
  }

  nav {
    display: none;
    position: fixed;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: #384D70;
    top: 0;
    left: 0;
    align-items: flex-start;
    justify-content: flex-start;
    row-gap: 30px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 94px;
  }

  nav a {
    color: var(--white);
    font-size: 19px;
    line-height: 23px;
  }

  .header__block a {
    z-index: 11;
  }

  .btn__community {
    z-index: 11;
  }

  nav .btn__community {
    display: block;
    width: 100%;
    margin-left: 0;
    text-align: center;
    max-width: 550px;

  }

}



@media(max-width: 720px) {
  .header__block img {
    margin-right: 0;
  }
}
