.play-to-earn {
  padding: 100px 0 175px;
}

.play-to-earn h2 {
  font-weight: 700;
  font-size: 50px;
  line-height: 60px;
  margin-bottom: 10px;
  text-align: center;
}

.play-to-earn__p {
  font-size: 25px;
  line-height: 42px;
  opacity: 0.5;
  margin-bottom: 50px;
  text-align: center;
}

.plot-and-goal__block {
  background-color: var(--light-black);
  border-radius: 30px;
  padding: 36px 50px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 50px;
  flex-wrap: wrap;
}

.plot-and-goal__block img {
  max-width: 649px;
  width: 100%;
}

.plot__block__txt {
  max-width: 946px;
}

.plot__block__txt h3 {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
}

.plot__block__p {
  font-size: 24px;
  line-height: 29px;
  color: rgba(255, 255, 255, 0.6);
  margin-top: 20px;
}

.earn-events__blocks {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.earn-events__block h3 {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 20px;
}

.earn-events__block p {
  font-size: 28px;
  line-height: 33px;
  opacity: 0.8;
  max-width: 570px;
}

.earn-events__block {
  border-radius: 30px;
  display: flex;
  max-width: 800px;
  max-height: 500px;
}

.earn__block {
  padding: 40px;
  background-color: var(--orange);

}

.earn__block img {
  max-width: 153px;
  width: 100%;
  margin-right: 30px;
}

.events__block {
  padding: 40px;
  background-color: var(--blue);
}

.earn__block__phone_img {
  max-width: 180px;
  width: 100%;
  margin-right: 30px;
}

.earn__block__coin_img {
  max-width: 153px;
  width: 100%;
  margin-right: 30px;
}

.plot__block__img {
  border-radius: 30px;
}

@media(max-width: 1735px) {
  .plot-and-goal__block {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .plot-and-goal__block img {
    margin-bottom: 30px;
  }

  .earn-events__block p {
    width: 100%;
  }

  .plot__block__txt {
    width: 100%;
    max-width: fit-content;
  }

  .earn-events__blocks {
    align-items: center;
    gap: 30px;
    justify-content: center;
  }

  .earn-events__block,
  .events__block {
    align-items: center;
    gap: 20px;
  }

  .earn-events__block h3 {
    text-align: center;
  }
}
@media(max-width: 850px) {
  .earn-events__block,
  .events__block {
    flex-direction: column;
  }
}
@media(max-width: 514px) {
  .play-to-earn {
    padding: 25px 0;
  }

  .play-to-earn__p {
    margin-bottom: 15px;
  }

  .earn-events__block h3 {
    font-size: 24px;
    line-height: 29px;
  }

  .earn-events__block p {
    font-size: 18px;
    line-height: 22px;
  }

  .plot__block__txt h3 {
    font-size: 24px;
    line-height: 29px;
  }

  .plot__block__p {
    font-size: 18px;
    line-height: 22px;
    margin-top: 15px;
  }

}
