.invest {
  background-color: var(--violet);
  padding: 150px 0 144px;
  position: relative;
}

.invest__block {
  display: flex;
  justify-content: space-between;
}

.invest__left-block {
  max-width: 552px;
  z-index: 2;
}

.invest__left-block h2 {
  font-weight: 700;
  font-size: 50px;
  line-height: 60px;
  margin-bottom: 20px;
}

.invest__left-block p {
  font-size: 20px;
  line-height: 24px;
  opacity: 0.6;
  margin-bottom: 40px;
}

.invest__btn {
  font-size: 25px;
  line-height: 30px;
  background-color: var(--red);
  padding: 25px 30px;
  display: inline-block;
  border-radius: 10px;
  cursor: pointer;
  transition: .5s;
}

.invest__btn:hover {
  background-color: var(--white);
  color: var(--red);
}
.invest__right-blocks {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.invest__right-block {
  display: flex;
}

.invest__right-block svg {
  margin-right: 20px;
}

.invest__right-block h3 {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 10px;
}

.invest__right-block p {
  max-width: 868px;
  font-size: 32px;
  line-height: 38px;
  opacity: 0.8;
}

.invest__svg {
  position: absolute;
  top: 0;
  left: 0;
}

@media(max-width: 1325px) {
  .invest {
      display: none;
  }
}
